
body{
    background-color: var(--tg-theme-bg-color);
    overflow-x: hidden !important;
}
html{
    overflow-x: hidden !important;
}
h2, h3, h4, h5, h6, p, hr{
    color: var(--tg-theme-text-color);
}
.row{
    position: relative;
}
.info{
    color: var(--tg-theme-text-color);
    background-color: var(--tg-theme-bg-color);
}
.other{
    opacity: 0.5;
}
.chart{
    border: 3px solid var(--tg-theme-button-color);
    border-radius: 2px;
    padding: 7px;
    overflow: hidden;
    position: relative;
}
.floatingTrigger{
    position: absolute;
    z-index: 99;
    right: 10px;
    font-size: 20px;
    color: grey;
}
button:not(.defaulted){
    background-color: var(--tg-theme-button-color) !important;
    color: var(--tg-theme-button-text-color) !important;
    border: none !important;
}
button.unselected{
    background-color: var(--tg-theme-bg-color) !important;
    color: var(--tg-theme-text-color) !important;
}
.MuiSlider-thumb, .MuiSwitch-thumb{
    background-color: white !important;
}
.Mui-checked .MuiSlider-thumb, .Mui-checked .MuiSwitch-thumb{
    background-color: var(--tg-theme-button-color) !important;
}
.MuiSlider-track, .MuiSwitch-track{
    background-color: var(--tg-theme-button-color) !important;
    border: none !important;
}
.MuiSlider-rail{
    background-color: color-mix(in srgb, var(--tg-theme-button-color) 38%, transparent) !important;
}
.MuiSlider-thumb:hover, .MuiSwitch-thumb:hover, .Mui-focusVisible{
    box-shadow: color-mix(in srgb, var(--tg-theme-button-color) 16%, transparent) 0px 0px 0px 8px !important;
}
.MuiSlider-mark{
    background-color: var(--tg-theme-bg-color) !important;
}
.google-visualization-tooltip{
    background-color: var(--tg-theme-bg-color) !important;
    max-width: 40vw;
}
.hiddenb{
    display: none;
}
.grow, .fall{
    font-family: 'Courier New', Courier, monospace;
    float: right;
}
.grow{
    color: darkgreen;
}
.fall{
    color: darkred;
}
.chart g>rect:nth-child(1){
    stroke-width: 0;
}
/* .select-off input{
    pointer-events: none !important;
} */
.flex-table{
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.nowrap{
    white-space: nowrap;
}